import { Sector } from "@rp/react/tanstack-api/workflow"
import { BriefStepsLegend } from "@rp/react/web/common/composite-components"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import {
  Box,
  MenuItem,
  OutlinedInput,
  Select,
  SelectChangeEvent,
  Typography,
} from "@rp/react/web/mui/core"
import { TrendingUp } from "@rp/react/web/mui/icons"

const renderSelectOptions = (sectors: Array<Sector>) => {
  return (
    sectors &&
    sectors.map((sector, index) => (
      <MenuItem key={`${sector}-${index}`} value={sector} role="menuitem">
        {sector}
      </MenuItem>
    ))
  )
}

const SectorSelector = ({
  sector,
  sectorsOptions,
  handleSectorSelection,
}: {
  sector?: Sector
  sectorsOptions: Array<Sector>
  handleSectorSelection: (sector: Sector) => void
}) => {
  const { isMobile } = useDeviceInfo()
  const handleChange = (event: SelectChangeEvent) => {
    handleSectorSelection(event.target.value as Sector)
  }

  const currentValue = sector || ""

  return (
    <>
      <BriefStepsLegend sx={{ alignItems: "center", mb: 2 }}>
        <Typography>{"Select a sector for your analysis:"}</Typography>
      </BriefStepsLegend>
      <Box
        bgcolor={"elevation.dp01"}
        p={2}
        borderRadius={1}
        mt={1.5}
        ml={!isMobile ? 7 : undefined}
      >
        <Box
          bgcolor={"elevation.dp08"}
          borderRadius={0.5}
          display={"flex"}
          alignItems={"center"}
          mb={1.5}
          width={"fit-content"}
          py={0.25}
          px={0.5}
        >
          <TrendingUp sx={{ mr: 1, height: "20px", width: "20px" }} />
          <Typography fontSize={"14px"} fontWeight={"bold"}>
            Sector
          </Typography>
        </Box>
        <Box mt={1}>
          <Select
            value={currentValue}
            label="Select a Sector"
            onChange={handleChange}
            inputProps={{
              "data-testid": "sector-options-id",
            }}
            sx={{ width: "100%" }}
            displayEmpty
            input={<OutlinedInput />}
            renderValue={selected => {
              if (selected.length === 0) {
                return <Typography>Select a Sector</Typography>
              }

              return selected
            }}
          >
            {renderSelectOptions(sectorsOptions)}
          </Select>
        </Box>
      </Box>
    </>
  )
}

export { SectorSelector }
